/**
 * @file Page View 
 */

export default function page(logger) {
  if (logger.appConfig.onhashchange) {
    logger.hashChangeTracker.addTracker(logger.send, logger);
  }

  logger.urlChangeTracker.addTracker(logger.send, logger);
  logger.pageVisibilityTracker.addPageloadTracker(logger.send, logger);
  logger.pageVisibilityTracker.addPageviewTracker(logger.send, logger);
  logger.pageVisibilityTracker.addPageVisibilityEventTracker(logger.send, logger);
  logger.pageVisibilityTracker.addPageUnloadTracker(logger.send, logger);

  return true;
}
