import LGet from 'lodash.get';
import LoggerBody from './body';
import { get, post } from './ping';
import HashChangeTracker from '../plugins/hash-change-tracker';
import UrlChangeTracker from '../plugins/url-change-tracker';
import PageVisibilityTracker from '../plugins/page-visibility-tracker';
import api from './modules/api';
import page from './modules/page';
import performance from './modules/performance';
import { noop } from '../utils/function';

function ELKFormatter(data = {}) {
  // @Param("imei") String imei,
  // @Param("uid") String uid,
  // @Param("ua") String ua,
  // @Param("hostname") String hostname,
  // @Param("pathname") String pathname,

  // @Param("app_name") String appName,
  // @Param("app_version") String appVersion,
  // @Param("pid") String pid,
  // @Param("pname") String pname,

  // @Param("type") String logtype,

  // @Param("api_url") String apiUrl,
  // @Param("api_time") Integer apiTime,
  // @Param("api_code") String apiCode,
  // @Param("api_status") Integer apiStatus,

  // @Param("p_ssl") long pSsl,
  // @Param("p_ttfb") long pTtfb,
  // @Param("p_trans") long pTrans,
  // @Param("p_dom") long pDom,
  // @Param("p_res") long pRes,
  // @Param("p_firstbyte") Long pFirstbyte,
  // @Param("p_ftp") Long pFtp,
  // @Param("p_tti") Long pTti,
  // @Param("p_ready") Long pReady,
  // @Param("p_load") Long pLoad

  // @Param("ext") String ext,
  const keys = Object.keys(data);
  const exitKeys = [
    'uid', 'imei', 'userAgent', 'hostname', 'pathname', 'appName', 'appVersion', 'appid', 'appname', 'type',
    'apiUrl', 'apiTime', 'appStatus', 'apiCode',
    'pDns', 'pTcp', 'pSsl', 'pTtfb', 'pTrans', 'pDom', 'pRes', 'pFirstbyte', 'pFpt', 'pTti', 'pReady', 'pLoad'
  ];
  const ELKLog = {
    uid: data.uid,
    imei: data.imei,
    ua: data.userAgent,
    hostname: data.hostname,
    pathname: data.pathname,
    app_name: data.appName,
    // app_version: data.appVersion,
    app_version: window.location.href,
    pid: data.appid,
    pname: data.appname,
    type: data.type,
    ext: {}
  };

  if (data.type === 'api') {
    ELKLog.api_url = data.apiUrl;
    ELKLog.api_time = data.apiTime;
    ELKLog.api_status = data.appStatus;
    ELKLog.api_code = data.apiCode;
  }

  if (data.type === 'p') {
    ELKLog.p_dns = data.pDns;
    ELKLog.p_tcp = data.pTcp;
    ELKLog.p_ssl = data.pSsl;
    ELKLog.p_ttfb = data.pTtfb;
    ELKLog.p_trans = data.pTrans;
    ELKLog.p_dom = data.pDom;
    ELKLog.p_res = data.pRes;
    ELKLog.p_firstbyte = data.pFirstbyte;
    ELKLog.p_ftp = data.pFpt;
    ELKLog.p_tti = data.pTti;
    ELKLog.p_ready = data.pReady;
    ELKLog.p_load = data.pLoad;
  }

  keys.forEach((key) => {
    if (exitKeys.indexOf(key) < 0) {
      ELKLog.ext[key] = data[key];
    }
  });

  ELKLog.ext = JSON.stringify(ELKLog.ext);
  return ELKLog;
}

/**
 * @file Logger 
 */

export default class Logger {
  constructor(appConfig) {
    const loggerBody = LGet(appConfig, 'logger.body');

    this.appConfig = appConfig;
    this.body = new LoggerBody(appConfig, loggerBody);
    this.hashChangeTracker = new HashChangeTracker({ shouldTrackHashChange: appConfig.onhashchange }); // eslint-disable-line
    this.urlChangeTracker = new UrlChangeTracker({ trackReplaceState: true });
    this.pageVisibilityTracker = new PageVisibilityTracker(appConfig);

    if (this.appConfig.onajax) {
      this.initApi();
    }

    if (this.appConfig.page) {
      this.initPage();
    }

    if (this.body.getIsSpeedLog()) {
      this.initPerformance();
    }
  }

  initApi() {
    api(this);
  }

  initPage() {
    page(this);
  }

  initPerformance() {
    performance(this);
  }

  get(data, callback) {
    // NOTE:  URL 
    const url = LGet(data, 'body.url', undefined);

    if (this.appConfig.ignoreUrl(url)) {
      return false;
    }

    const body = ELKFormatter(this.body.getBody(data.body));
    console.log(body);

    return get(body, data.url || this.appConfig.logger.url, callback);
  }

  post(data, callback) {
    // NOTE:  URL 
    const url = LGet(data, 'body.url', undefined);

    if (this.appConfig.ignoreUrl(url)) {
      return false;
    }

    const body = ELKFormatter(this.body.getBody(data.body));
    console.log(body);

    return post(body, data.url || this.appConfig.logger.url, callback);
  }

  send(data = {}, callback = noop) {
    if (this.appConfig.disabled) {
      console.error('');
      return false;
    }

    if (!this.body.getIsLog()) {
      console.error('');
      return false;
    }

    const type = (data.type || LGet(this.appConfig, 'logger.methods')).toLowerCase();

    return this[type](data, callback);
  }
}
