import * as img from '../utils/img';
import * as string from '../utils/string';
import * as uuid from '../utils/uuid';
import { noop } from '../utils/function';

function imgPing(imgUrl, data, url, callback = noop) {
  const src = imgUrl;
  const id = uuid.uuid();
  const key = `${id}_IMGPING_MFM`;
  const imgCallback = () => {
    window[key] = null;
    callback(true, data, url);
  };

  window[key] = img.create(src);
  window[key].onload = imgCallback;
  window[key].onerror = imgCallback;

  return true;
}

function xhrPing(data, url, callback = noop) { // NOTE: 
  let xhr = new window.XMLHttpRequest();

  if (!xhr) {
    return callback(false, data, url);
  }

  xhr.open('POST', url, true);
  // xhr.setRequestHeader('Content-Type', 'application/json;charset=UTF-8');
  // xhr.setRequestHeader('Content-Type', 'text/plain;charset=UTF-8');
  xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded;charset=UTF-8');
  xhr.onreadystatechange = () => {
    if (xhr.readyState === 4) {
      callback(true, data, url);
      xhr = null;
    } else {
      callback(false, data, url);
    }
  };

  xhr.send(string.params(data));
  return true;
}

function beaconPing(data, url, callback = noop) {
  const headers = { type: 'application/x-www-form-urlencoded;charset=UTF-8' };
  const blob = new window.Blob([string.params(data)], headers);

  const isSend = window.navigator.sendBeacon(
    url,
    blob
  );

  callback(isSend, data, url);
  return isSend;
}

function smartPost(data, url, callback = noop) {
  if (window.navigator.sendBeacon) {
    return beaconPing(data, url, callback);
  }

  return xhrPing(data, url, callback);
}

// https://gist.github.com/zmmbreeze/ddb4b3a619187b923dc2c009b4323a42#file-analytics-js-L317
// NOTE:  get  post content-type: application/x-www-form-urlencoded
function smartGet(data, url, callback = noop) {
  // if content maximum HTTP GET allowed length, use smartPost
  // if content maximum HTTP POST allowed length, no send
  const query = string.params(data);
  const imgUrl = `${url}?${query}`;

  if (query.length <= 2036) {
    return imgPing(imgUrl, data, url, callback);
  }

  return smartPost(data, url, callback);
}

/**
 * mfm.logger.get  get 
 *
 * @author  <guojunlin1@xiaomi.com>
 * @author  <wenqiao1@xiaomi.com>
 * @export
 * @param {*} data
 * @param {String} url 
 * @param {Function} [callback=noop] callback(isSend:Boolean, data:<Any>, url:String)
 * @returns
 */
export function get(data, url, callback = noop, length = 0) {
  return smartGet(data, url, callback, length);
}

/**
 * mfm.logger.post  post 
 *
 * @author  <guojunlin1@xiaomi.com>
 * @author  <wenqiao1@xiaomi.com>
 * @export
 * @param {*} data
 * @param {String} url 
 * @param {Function} [callback=noop] callback(isSend:Boolean, data:<Any>, url:String)
 * @returns
 */
export function post(data, url, callback = noop) {
  return smartPost(data, url, callback);
}
