/**
 * @file hash 
 */
import { getPath } from '../utils/location';
import * as win from '../utils/window';

function onhashchangePolyfill() {
  (function (window) { // eslint-disable-line
    // exit if the browser implements that event
    if ('onhashchange' in window) {
      return;
    }

    const { location } = window.location;
    let oldURL = location.href;
    let oldHash = location.hash;

    // check the location hash on a 100ms interval
    window.setInterval(function () { // eslint-disable-line
      const newURL = location.href;
      const newHash = location.hash;

      // if the hash has changed and a handler has been bound...
      if (newHash !== oldHash && typeof window.onhashchange === 'function') {
        // execute the handler
        window.onhashchange({
          type: 'hashchange',
          oldURL,
          newURL
        });

        oldURL = newURL;
        oldHash = newHash;
      }
    }, 100);
  })(window);
}

/**
 * Class for the `urlChangeTracker` plugin.
 */
export default class HashChangeTracker {
  /**
   * Adds handler for the history API methods
   * @param {!Tracker} tracker Passed internally by analytics.js
   * @param {?Object} opts Passed by the require command.
   */
  constructor(opts) {
    onhashchangePolyfill();

    // Feature detects to prevent errors in unsupporting browsers.
    if (!window.addEventListener) {
      throw new Error('use addEventListener in unsupporting browsers');
    }

    /** @type {UrlChangeTrackerOpts} */
    const defaultOpts = {
      shouldTrackHashChange: this.shouldTrackHashChange
    };

    this.opts = Object.assign(defaultOpts, opts);
    this.trackerQeueu = [];

    // Sets the initial page field.
    // Don't set this on the tracker yet so campaign data can be retreived
    // from the location field.
    this.path = getPath();

    window.addEventListener('hashchange', this.handleHashChange, false);
  }

  addTracker(trackerFun, context = null) {
    this.trackerQeueu.push({
      tracker: trackerFun,
      context
    });
  }

  tracker(data) {
    this.trackerQeueu.forEach((item) => {
      item.tracker.call(item.context, data);
    });
  }

  /**
   * Updates the page and title fields on the tracker and sends a pageview
   * if a new history entry was created.
   */
  handleHashChange() {
    const timestamp = Date.now();

    // Calls the update logic asychronously to help ensure that app logic
    // responding to the HASH change happens prior to this.
    setTimeout(() => {
      const oldPath = this.path;
      const newPath = getPath();

      if (oldPath !== newPath
        && this.opts.shouldTrackUrlChange.call(this, newPath, oldPath)) {
        this.path = newPath;

        const log = {
          body: {
            timestamp,
            type: 'pv',
            url: window.location.href,
            hostname: window.location.hostname,
            pathname: window.location.pathname,
            title: window.document.title,
            referer: window.document.referrer,
            visibility: win.getVisibilityState()
          }
        };

        //  hash  tracker
        this.tracker(log);
      }
    }, 0);
  }

  /**
   * Determines whether or not the tracker should send a hit with the new page
   * data. This default implementation can be overrided in the config options.
   * @param {string} newPath The path after the URL change.
   * @param {string} oldPath The path prior to the URL change.
   * @return {boolean} Whether or not the URL change should be tracked.
   */
  shouldTrackHashChange(newPath, oldPath) { // eslint-disable-line
    return !!(newPath && oldPath);
  }
}
