/**
 * @file Page Load 
 */
import * as win from '../../utils/window';

function getEntriesTime(types, logger) {
  const entry = [];

  types.forEach((type) => {
    // https://developer.mozilla.org/en-US/docs/Web/API/Performance/getEntries#Browser_compatibility
    // Safari 11 
    if (window.performance && window.performance.getEntries) {
      window.performance.getEntries({ entryType: type }).forEach((item) => {
        const data = {
          type: item.initiatorType,
          name: item.name,
          duration: item.duration,
          time: item.startTime + item.duration // 
        };

        // 
        if (item.entryType === 'resource'
          && item.initiatorType !== 'xmlhttprequest'
          && item.initiatorType !== 'beacon') {
          const timeout = item.initiatorType === 'img' ? logger.appConfig.resource.imgTimeout : logger.appConfig.resource.timeout;

          if (item.duration > timeout) {
            entry.push(data);
          }
        } else if (entry.entryType === 'paint') {
          entry.push({
            type: 'paint',
            name: item.name,
            duration: item.duration,
            time: item.startTime + item.duration
          });
        }
      });
    }
  });

  return entry;
}

export default function performance(logger) {
  // TODO: async  load
  window.addEventListener('load', function (e) { // eslint-disable-line
    // https://developer.mozilla.org/en-US/docs/Web/API/Performance/getEntries#Browser_compatibility
    // Safari 11 
    let timing = {};
    if (window.performance.timing) {
      timing = window.performance.timing; // eslint-disable-line
    }
    const body = {
      type: 'p',
      timestamp: Date.now(),
      url: window.location.href,
      hostname: window.location.hostname,
      pathname: window.location.pathname,
      title: window.document.title,
      referer: window.document.referrer,
      visibility: win.getVisibilityState(),
      pDns: timing.domainLookupEnd - timing.domainLookupStart,
      pTcp: timing.connectEnd - timing.connectStart,
      pSsl: timing.connectEnd - timing.secureConnectionStart,
      pTtfb: timing.responseStart - timing.requestStart,
      pTrans: timing.responseEnd - timing.responseStart,
      pDom: timing.domInteractive - timing.responseEnd,
      pRes: timing.loadEventStart - timing.domContentLoadedEventEnd,
      pFirstbyte: timing.responseStart - timing.domainLookupStart,
      pFpt: timing.responseEnd - timing.fetchStart,
      pTti: timing.domInteractive - timing.fetchStart,
      pReady: timing.domContentLoadedEventEnd - timing.fetchStart,
      pLoad: timing.loadEventStart - timing.fetchStart,
      pEntry: getEntriesTime(['paint', 'resource'], logger)
    };

    logger.send({
      body
    });
  });

  return true;
}
