import LMerge from 'lodash.merge';
import LGet from 'lodash.get';
// import Raven from 'raven-js';
import appDefaultConfig from './config/app.default';
import Logger from './logger';
import { noop } from './utils/function';

class Mfm {
  constructor(defaultConfig, CLogger) {
    this.$config = defaultConfig;
    this.queue = [];
    this.setConfig();
    this.initQueue();

    this.logger = new CLogger(this.config);
    // eslint-disable-line
    // Raven.config(this.config.sentry.url, this.config.sentry.options).install();
    // this.Raven = Raven;

    // const loggerBody = this.logger.body.get();

    // Raven.setUserContext({
    //   imei: loggerBody.imei,
    //   uid: loggerBody.uid
    // }); // eslint-disable-line

    // clear quene
    this.cleanQuene();
  }

  setConfig() {
    try {
      if (window.mfm.config) {
        // merge default config
        this.config = LMerge({}, this.$config, window.mfm.config);
      } else {
        this.config = this.$config;
      }
    } catch (e) {
      throw new Error('Please use mfm.js by the correct way!');
    }
  }

  initQueue() {
    try {
      if (window.mfm.queue) {
        // concat user queue
        this.queue = window.mfm.queue.concat(this.queue);
      }
    } catch (e) {
      throw new Error('Please use mfm.js by the correct way!');
    }
  }

  cleanQuene() {
    this.queue.forEach((item) => {
      this.send(item);
    });
  }

  send(data = {}, callback = noop) {
    const type = (data.type || LGet(this.config, 'logger.methods')).toLowerCase();

    if (this.logger && this.logger[type]) {
      return this.logger[type](data, callback);
    }

    this.queue.push(data);
    return false;
  }
}

window.mfm = new Mfm(appDefaultConfig, Logger);
