/**
 * @file logger body
 */
import cookie from 'js-cookie';
import LMerge from 'lodash.merge';
import config from '../config';
import * as uuid from '../utils/uuid';

function getLoogerBodyItem(key, defaultValue, expires) {
  const currentCookie = cookie.get(`${config.prefix}${key}`);

  if (currentCookie) {
    return currentCookie;
  }

  cookie.set(`${config.prefix}${key}`, defaultValue, {
    expires: new Date(Date.now() + expires),
    path: '/'
  });

  return defaultValue;
}

function setLoogerBodyItem(key, value) {
  if (!value) {
    return false;
  }

  cookie.set(`${config.prefix}${key}`, value);

  return value;
}

export default class LoggerBody {
  constructor(appConfig, loggerBody = {}) {
    this.appConfig = appConfig;
    this.$set(appConfig, loggerBody);
  }

  /**
   *  Logger 
   *
   * @param {Logger} data
   */
  $set(appConfig, data = {}) {
    const cookieBody = {
      imei: uuid.uuid(),
      uid: uuid.uuid(),
      did: uuid.uuid(),
      isLog: Number(Math.ceil(Math.random() * 100) <= this.appConfig.sampleRate),
      isSpeedLog: Number(Math.ceil(Math.random() * 100) <= this.appConfig.siteSpeedSampleRate)
    };
    const body = {
      appVersion: window.navigator.appVersion,
      appName: window.navigator.appName,
      appid: this.appConfig.appid,
      appname: this.appConfig.appname
    };
    const cookieBodyKeys = Object.keys(cookieBody);
    const bodyKeys = Object.keys(body);

    for (let cbkey of cookieBodyKeys) { // eslint-disable-line
      this[cbkey] = getLoogerBodyItem(cbkey, data[cbkey] || cookieBody[cbkey], appConfig.expires);
    }

    for (let bkey of bodyKeys) { // eslint-disable-line
      this[bkey] = data[bkey] || body[bkey];
    }

    // set sessionId
    LoggerBody.$setSessionId();
  }

  get(data = {}) {
    const body = {
      imei: this.imei,
      uid: this.uid,
      did: this.did,
      appVersion: this.appVersion,
      appName: this.appName,
      appid: this.appid,
      appname: this.appname,

      network: window.navigator.connection && window.navigator.connection.effectiveType,
      userAgent: window.navigator.userAgent
    };

    body.sessionId = LoggerBody.$getSessionId();

    // merge data to logger body
    const nextBody = LMerge({}, body, data);

    // URL id  * 
    if (nextBody.url) {
      nextBody.url = this.appConfig.parseUrl(nextBody.url);
    }

    //  URL 
    if (nextBody.title) {
      nextBody.title = this.appConfig.parseTitle(nextBody.url);
    }

    return nextBody;
  }

  getIsLog() {
    return Boolean(Number(this.isLog));
  }

  getIsSpeedLog() {
    return Boolean(Number(this.isSpeedLog));
  }

  static $setSessionId() {
    const sessionId = uuid.uuid();
    cookie.set(`${config.prefix}sessionId`, sessionId, { expires: new Date(Date.now() + config.session.expires) });

    return sessionId;
  }

  static $getSessionId() {
    const sessionId = cookie.get(`${config.prefix}sessionId`);
    //  sessionId 
    // OPTIMIZE:  cookie  localStorage 
    if (sessionId) {
      return sessionId;
    }

    return LoggerBody.$setSessionId();
  }

  /**
   *  Logger imei, uid 
   *
   * @param {Logger} data
   */
  update(data) {
    const dkeys = Object.key(data);

    for (let dkey of dkeys) { // eslint-disable-line
      if (this[dkey]) {
        this[dkey] = setLoogerBodyItem(dkey, data[dkey]);
      }
    }
  }

  /**
   *  logger body 
   *
   * @return {Object} logger Logger logger.parameters  String
   */
  getBody(data = {}) {
    const body = Object.assign({}, this.get(data));

    return body;
  }
}
