/**
 * @file uuid 
 */

export function $uuid() {
  return String(Math.round(2147483647 * Math.random()));
}

export function uuid() {
  try {
    //  crypto API  uuid
    const u32a = new Uint32Array(1);
    window.crypto.getRandomValues(u32a);

    return String(u32a[0] & 2147483647); // eslint-disable-line
  } catch (b) {
    return $uuid();
  }
}
