/**
 * @file api ajax 
 */
import { uuid } from '../../utils/uuid';
import * as url from '../../utils/url';
import * as win from '../../utils/window';

function initialAjaxListener(window) {
  (function (window) { // eslint-disable-line
    try {
      // a : While a window.CustomEvent object exists, it cannot be called as a constructor.
      // b : There is no window.CustomEvent object
      new window.CustomEvent('T'); // eslint-disable-line
    } catch (e) {
      function CustomEvent(event, params = {}) { // eslint-disable-line
        const nextParams = Object.assign({ bubbles: false, cancelable: false, detail: undefined },
          params);
        const evt = window.document.createEvent('CustomEvent');

        evt.initCustomEvent(event, nextParams.bubbles, nextParams.cancelable, nextParams.detail);
        return evt;
      }

      CustomEvent.prototype = window.Event.prototype;
      window.CustomEvent = CustomEvent; // eslint-disable-line
    }
  })(window);

  function ajaxEventTrigger(event) {
    const ajaxEvent = new window.CustomEvent(event, { detail: this });
    window.dispatchEvent(ajaxEvent);
  }

  const OriginXHR = window.XMLHttpRequest;

  function OverrideXHR() {
    const overrideXHR = new OriginXHR();
    const originXHROpen = overrideXHR.open;

    overrideXHR.open = function overrideXHROpen(method, url, async = true, user = null, password = null) { // eslint-disable-line
      overrideXHR.requestURL = url; // eslint-disable-line
      return originXHROpen.apply(this, arguments); // eslint-disable-line
    };

    overrideXHR.addEventListener('loadstart', function ajaxLoadStart() { ajaxEventTrigger.call(this, 'ajaxLoadStart'); }, false);
    overrideXHR.addEventListener('progress', function ajaxProgress() { ajaxEventTrigger.call(this, 'ajaxProgress'); }, false);
    overrideXHR.addEventListener('abort', function ajaxAbort() { ajaxEventTrigger.call(this, 'ajaxAbort'); }, false);
    overrideXHR.addEventListener('error', function ajaxError() { ajaxEventTrigger.call(this, 'ajaxError'); }, false);
    overrideXHR.addEventListener('load', function ajaxLoad() { ajaxEventTrigger.call(this, 'ajaxLoad'); }, false);
    overrideXHR.addEventListener('timeout', function ajaxTimeout() { ajaxEventTrigger.call(this, 'ajaxTimeout'); }, false);
    overrideXHR.addEventListener('loadend', function ajaxLoadEnd() { ajaxEventTrigger.call(this, 'ajaxLoadEnd'); }, false);
    overrideXHR.addEventListener('readystatechange', function ajaxReadyStateChange() { ajaxEventTrigger.call(this, 'ajaxReadyStateChange'); }, false);

    return overrideXHR;
  }

  window.XMLHttpRequest = OverrideXHR; // eslint-disable-line
}

export default function api(logger) {
  initialAjaxListener(window);

  const XHRMap = {};
  const getCommonLoggerBody = (xhr, data = {}) => {
    let response;

    try {
      response = JSON.stringify(xhr.response); // TODO:  JSON
    } catch (e) {
      response = {};
    }

    const { requestURL } = xhr;
    const log = {
      body: {
        type: 'api',
        timestamp: Date.now(),
        url: window.location.href,
        hostname: window.location.hostname,
        pathname: window.location.pathname,
        title: window.document.title,
        referer: window.document.referrer,
        visibility: win.getVisibilityState(),
        apiUrl: url.parse(requestURL, 'pathname'),
        apiStatus: xhr.status,
        apiCode: response.errCode
      }
    };

    const nextLog = Object.assign({}, log, data);
    return nextLog;
  };
  const isIgnoreAPI = (xhr) => { // eslint-disable-line
    const pathname = url.parse(xhr.requestURL, 'pathname');

    return logger.appConfig.ignoreUrl(pathname);
  };

  window.addEventListener('ajaxLoadStart', function (e) { // eslint-disable-line
    // XMLHttpRequest Object
    // console.log('ajaxLoadStart', e.detail);
    e.detail.id = uuid();
    XHRMap[e.detail.id] = window.performance.now();
  });

  // window.addEventListener('ajaxProgress', function (e) { // eslint-disable-line
  //   // XMLHttpRequest Object
  //   console.log('ajaxProgress', e.detail);
  // });

  // window.addEventListener('ajaxAbort', function (e) { // eslint-disable-line
  //   // XMLHttpRequest Object
  //   console.log('ajaxAbort', e.detail);
  // });

  window.addEventListener('ajaxError', function (e) { // eslint-disable-line
    // XMLHttpRequest Object
    // console.log('ajaxError', e.detail);
    if (!isIgnoreAPI(e.detail)) {
      logger.send(getCommonLoggerBody(e.detail, {
        apiTime: window.performance.now() - XHRMap[e.detail.id]
      }));
      delete XHRMap[e.detail.id];
    }
  });

  // window.addEventListener('ajaxLoad', function (e) { // eslint-disable-line
  //   // XMLHttpRequest Object
  //   console.log('ajaxLoad', e.detail);
  // });

  window.addEventListener('ajaxTimeout', function (e) { // eslint-disable-line
    // XMLHttpRequest Object
    // console.log('ajaxTimeout', e.detail);
    if (!isIgnoreAPI(e.detail)) {
      logger.send(getCommonLoggerBody(e.detail, {
        apiTime: window.performance.now() - XHRMap[e.detail.id]
      }));
      delete XHRMap[e.detail.id];
    }
  });

  window.addEventListener('ajaxLoadEnd', function (e) { // eslint-disable-line
    // XMLHttpRequest Object
    // console.log('ajaxLoadEnd', e.detail);
    if (!isIgnoreAPI(e.detail)) {
      logger.send(getCommonLoggerBody(e.detail, {
        apiTime: window.performance.now() - XHRMap[e.detail.id]
      }));
      delete XHRMap[e.detail.id];
    }
  });

  // window.addEventListener('ajaxReadyStateChange', function (e) { // eslint-disable-line
  //   // XMLHttpRequest Object
  //   console.log('ajaxReadyStateChange', e.detail);
  // });

  return true;
}
